.sendForm {
    display: flex;
    align-items: center;
    padding-top: 73px;
    padding-left: 70px;
}

.textarea {
    min-width: 976px;
    min-height: 40px;
    background: #F5F7FB;
    box-shadow: 0px 5px 20px rgba(29, 33, 38, 0.03), 0px 1px 2px rgba(29, 33, 38, 0.1);
    border-radius: 20px;
    overflow: auto;
    overflow:hidden;
    resize: none;
    padding: 12px 24px 11px 24px;
    border: none;
    /*text*/
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 17px;
}

.button {
    width: 120px;
    height: 40px;
    margin-left: 44px;
    background: #0066CC;
    border-radius: 20px;
    border: none;
    /*text*/
    font-style: normal;
    font-weight: 600;
    font-size: 16px;
    line-height: 20px;
    color: #FFFFFF;
    cursor: pointer;
}

.button:hover {
    background: rgba(0, 102, 204, 0.87);
}
.button:active {

}
